import { h, Component } from 'preact';
import { Router } from 'preact-router';

import Header from './header';

// Code-splitting is automated for routes
import Home from '../routes/home';
import Databox from '../routes/databox';
import Buttonkit from '../routes/buttonkit';
import CinC from '../routes/cinc';

export default class App extends Component {

	/** Gets fired when the route changes.
	 *	@param {Object} event		"change" event from [preact-router](http://git.io/preact-router)
	 *	@param {string} event.url	The newly routed URL
	 */
	handleRoute = e => {
		this.currentUrl = e.url;
	};
	//<Header />
	render() {
		return (
			<div id="app">

				<Router onChange={this.handleRoute}>
					<Home path="/sc" />
					<Databox path="/sc/project/databox" item="about" />
					<Buttonkit path="/sc/project/buttonkit" item="about" />
					<Buttonkit path="/sc/project/buttonkit/tech" item="tech" />
					<Buttonkit path="/sc/project/buttonkit/design" item="design" />
					<Databox path="/sc/project/databox/my" item="my" />
					<Databox path="/sc/project/databox/tech" item="tech" />
					<Databox path="/sc/project/databox/papers" item="papers" />
					<Databox path="/sc/project/databox/design" item="design" />
					<CinC path="/sc/project/cinc" item="about"/>
				</Router>
			</div>
		);
	}
}




